export default function threeCircles(parent, cfg) {
  var _cfg = {
    id: "threeCircles",
    prefix: "",
    radius: 50,
    overlap_dis: 30,
    // colors: ["#ff0000", "#00ff00", "#0000ff"]
    colors: ["cyan", "magenta", "yellow"]
  }

  for (var prop in _cfg) {
    if (cfg[prop]) {
      _cfg[prop] = cfg[prop];
    }
  }

  var radius = _cfg.radius,
    overlap_dis = _cfg.overlap_dis,
    colors = _cfg.colors,
    prefix = _cfg.prefix;

  var circle_center_dis = radius * 2 - overlap_dis;

  var g = parent.append("g").attr("id", _cfg.id);

  g.append("circle")
    .attr("id", prefix + "top-circle")
    .attr("cx", radius + circle_center_dis / 2)
    .attr("cy", radius)
    .attr("r", radius)
    .style("fill", colors[0]);

  g.append("circle")
    .attr("id", prefix + "left-circle")
    .attr("cx", radius)
    .attr("cy", radius + circle_center_dis * Math.sqrt(3) / 2)
    .attr("r", radius)
    .style("fill", colors[1]);

  g.append("circle")
    .attr("id", prefix + "right-circle")
    .attr("cx", radius + circle_center_dis)
    .attr("cy", radius + circle_center_dis * Math.sqrt(3) / 2)
    .attr("r", radius)
    .style("fill", colors[2]);

  return g;
}


