<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <aside>
      <ol>
        <li v-for="demo in demos" :key="demo.name">
          <router-link :to="demo.path">{{demo.name}}</router-link>
        </li>
      </ol>
    </aside>
    <main>
      <router-view/>
    </main>
  </div>
</template>

<script>
import demoRoutes from "@/demoComponents"

export default {
  name: 'App',
  data: function(){
    return {
      demos: demoRoutes
    }
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 10px;
}

aside,main{
  float: left;
}

aside{
  width: 300px;
  border-right: 1px solid #000;
  background-color: #eee;
}

main{
  width: 800px;

  margin: 0 auto;
  padding-left: 20px;
  border-left: 1px dashed #cccccc;

  box-sizing: border-box;
}

ol,li{
  /* list-style: none; */
  text-align: left;
  line-height: 25px;
}

</style>



