import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import demoRoutes from "@/demoComponents"

Vue.use(Router)


const hello = {
  name: "HelloWorld",
  path: "/HelloWorld",
  component: HelloWorld
};

demoRoutes.unshift(hello);

export default new Router({
  routes: demoRoutes
})


