<template>
  <div id="chart">

  </div>
</template>

<script>
export default {
  name: "RingGradient",
  mounted: function(){
    RingGradientMethod("#chart");
  }
}

function RingGradientMethod(id = "#chart") {
  console.log("RingGradient")

  var colors = ["red", "blue", "green"];

  var width = 600,
    height = 600;

  var div = d3.select(id);
  var svg = div.append("svg")
    .attr("width", width)
    .attr("height", height);

  var defs = svg.append("defs");
  var g1 = defs.append("linearGradient").attr("id", "g1")
  g1.append("stop").attr("stop-color", colors[0]).attr("offset", "0%")
  g1.append("stop").attr("stop-color", colors[1]).attr("offset", "100%")
  g1.attr("x1", "0%").attr("y1", "0%").attr("x2", "0%").attr("y2", "100%");

  var g2 = defs.append("linearGradient").attr("id", "g2")
  g2.append("stop").attr("stop-color", colors[2]).attr("offset", "0%")
  g2.append("stop").attr("stop-color", colors[1]).attr("offset", "100%")
  g2.attr("x1", "0%").attr("y1", "0%").attr("x2", "0%").attr("y2", "100%");


  var arc = d3.arc().innerRadius(150).outerRadius(200);

  var arcs = [
    { startAngle: 0, endAngle: Math.PI },
    { startAngle: Math.PI, endAngle: Math.PI * 2 }
  ];
  var gs = ["g1", "g2"];

  svg.selectAll("path")
    .data(arcs)
    .enter()
    .append("path")
    .attr("transform", "translate(" + width / 2 + "," + height / 2 + ")")
    .attr("d", arc)
    .attr("fill", function(d, i) {
      return "url(#" + gs[i] + ")"
    })
}
</script>




