function Filter(parent, cfg) {
  this._init(parent, cfg);
}

const _injectAttrs = function(elem, cfg) {
  if (cfg instanceof Object) {
    for (let attr in cfg) {
      elem.attr(attr, cfg[attr]);
    }
  }
}

Filter.prototype._init = function(parent, cfg) {
  this.parent = parent;
  this.elem = parent.append("filter");

  _injectAttrs(this.elem, cfg);
}

Filter.prototype.append = function(domStr, cfg) {
  const elem = this.elem.append(domStr);
  _injectAttrs(elem, cfg);
  return elem;
}

export default Filter;


