<template>
  <div id="chart"></div>
</template>

<script>
import LinearGradient from "@/helper/linearGradient";
import Filter from "@/helper/filter";

export default {
  name: "GradientOverlapingArea",
  mounted: function() {
    chart("#chart");
  }
};

function chart(id = "#chart") {
  var data = [
    [240, 255, 408, 388, 420, 495],
    [392, 370, 305, 325, 265, 270],
    [295, 180, 268, 195, 155, 310]
  ];

  data.sort(function(a, b) {
    return d3.max(b) - d3.max(a);
  });

  var W = 600,
    H = 446;
  var margin = {
    top: 89,
    right: 24,
    bottom: 86,
    left: 79
  };

  var width = W - margin.right - margin.left,
    height = H - margin.top - margin.bottom;

  var x = d3
      .scaleLinear()
      .domain([0, data[0].length - 1])
      .range([0, width]),
    y = d3
      .scaleLinear()
      .domain([0, 500])
      .range([height, 0]);

  var colors = ["#45A2FA", "#FACB48", "#FF8A97"];
  var color = d3
    .scaleOrdinal()
    .domain(d3.range(data.length))
    .range(colors);

  var area = d3
    .area()
    .x(function(d, i) {
      return x(i);
    })
    .y0(height)
    .y1(function(d) {
      return y(d);
    })
    .curve(d3.curveCardinal);

  var line = d3
    .line()
    .x(function(d, i) {
      return x(i);
    })
    .y(function(d) {
      return y(d);
    })
    .curve(d3.curveCardinal);

  var div = d3.select(id);
  var svg = div
    .append("svg")
    .attr("width", W)
    .attr("height", H)
    .style("background-color", "#eee");

  var defs = svg.append("defs");

  var filterId = "filter";
  createGaussian(defs, filterId);

  d3.range(data.length).forEach(function(i) {
    var c = colors[i];
    new LinearGradient()
      .defs(defs)
      .id("gradient-" + i)
      .x1("0%")
      .y1("0%")
      .x2("0%")
      .y2("100%")
      .appendStop("0%", c, "1")
      .appendStop("100%", c, "0");
  });

  var g = svg
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

  var gs = g
    .append("g")
    .selectAll("g")
    .data(data)
    .enter()
    .append("g");

  gs
    .append("path")
    .attr("d", area)
    .style("fill", fillGradient)
    .style("fill-opacity", 0.2);

  gs
    .append("path")
    .attr("d", line)
    .style("fill", "none")
    .style("stroke", fill)
    .style("stroke-width", 2)
    .attr("filter", "url(#" + filterId + ")");

  svg
    .append("g")
    .attr("transform", "translate(" + 64 + "," + margin.top + ")")
    .call(d3.axisLeft(y).ticks(5))
    .style("font-size", "14px")
    .selectAll("path,line")
    .remove();

  svg
    .append("g")
    .attr(
      "transform",
      "translate(" + margin.left + "," + (H - margin.bottom) + ")"
    )
    .call(
      d3
        .axisBottom(x)
        .ticks(5)
        .tickPadding(10)
    )
    .style("font-size", "14px");

  svg
    .append("text")
    .attr("x", 29)
    .attr("y", 54)
    .style("text-anchor", "start")
    .style("font-size", "20px")
    .text("面积图");

  function fill(d, i) {
    return colors[i];
  }

  function fillGradient(d, i) {
    return "url(#gradient-" + i + ")";
  }
}

function createGaussian(defs, id) {
  const filter = new Filter(defs, { id: id });

  filter.append("feGaussianBlur", {
    in: "SourceGraphic",
    stdDeviation: 8,
    result: "blur"
  });

  filter.append("feOffset", {
    dx: 0,
    dy: 2,
    result: "blur"
  });

  filter.append("feComposite", {
    in: "SourceGraphic",
    in2: "blur",
    operator: "over"
  });
}
</script>



