import RingGradient from "./RingGradient"
import GradientOverlapingArea from "./GradientOverlapingArea"
import SvgFilterTest from "./SvgFilterTest"
import ColorsBlend from "./ColorsBlend"

const demos = [{
    name: "RingGradient",
    path: "/RingGradient",
    component: RingGradient
  },
  {
    name: "GradientOverlapingArea",
    path: "/GradientOverlapingArea",
    component: GradientOverlapingArea
  },
  {
    name: "SvgFilterTest",
    path: "/SvgFilterTest",
    component: SvgFilterTest
  },
  {
    name: "ColorsBlend",
    path: "/ColorsBlend",
    component: ColorsBlend
  }
];

export default demos;


