function LinearGradient() {
  this.elem = null;
  this._id = "";
}

var linearGradientProto = LinearGradient.prototype;

linearGradientProto.defs = function(defs) {
  this.elem = defs.append("linearGradient");
  return this;
}

linearGradientProto.id = function(id) {
  if (!arguments.length) {
    return this._id;
  }
  this._id = id;
  this.elem.attr("id", id);
  return this;
}

linearGradientProto.appendStop = function(offset, stopColor, stopOpacity) {
  this.elem.append("stop")
    .attr("offset", offset)
    .attr("stop-color", stopColor)
    .attr("stop-opacity", stopOpacity);
  return this;
}

linearGradientProto.x1 = function(x) {
  this.elem.attr("x1", x);
  return this;
}

linearGradientProto.y1 = function(y) {
  this.elem.attr("y1", y);
  return this;
}
linearGradientProto.x2 = function(x) {
  this.elem.attr("x2", x);
  return this;
}
linearGradientProto.y2 = function(y) {
  this.elem.attr("y2", y);
  return this;
}

export default LinearGradient;


